import React, { useContext, useEffect, useState } from "react";
import { NavLink, Link } from "react-router-dom";
import AuthContext from "../../store/auth-context";
import { getCookie } from "../../Cookie";

import "./NavBar.css";
import logo from "../../img/logo.png";

const NavBar = () => {
  const authCtx = useContext(AuthContext);

  const isLoggedIn = !!getCookie("accessToken");

  const logoutHandler = () => {
    authCtx.logout();
  };

  return (
    <header className="navbar">
      <div className="navbar__left">
        <Link to="/">
          <div className="nav-logo">
            <div>
              <img src={logo} alt="" height="30px" />
            </div>
            <h2>WATCH ME</h2>
          </div>
        </Link>
        <nav className="nav-list">
          <ul>
            <li>
              <NavLink to="RoomRecruit"> </NavLink>
            </li>
            <li>
              <NavLink to="GroupRecruit">  </NavLink>
            </li>
            {/* <li>
              <NavLink to="About"></NavLink>
            </li> */}
          </ul>
        </nav>
      </div>
      <div className="navbar__right">
        {/*   */}
        {isLoggedIn && (
          <div className="dropdown__login">
            {/* ,  */}
            <div className="user-info">
              <img
                src={authCtx.userData.profileImage}
                alt="profile"
                id="nav-profile-picture"
              />
              <span className="nav-username">{authCtx.userData.nickName}</span>
            </div>

            {/* dropdown */}
            <div className="dropdowncontent__login">
              <a onClick={logoutHandler}></a>
              <Link to="/MyPage"></Link>
              <Link to="/MyGroups"></Link>
            </div>
          </div>
        )}
        {/*  */}
        {!isLoggedIn && <Link to="/login"></Link>}
      </div>
    </header>
  );
};

export default NavBar;
