import React from "react";
import "./FindID.css";
import { useContext, useState } from "react";
import { FetchUrl } from "../../../store/communication";
import { Link, useNavigate } from "react-router-dom";
import swal from "sweetalert";
import ErrorCode from "../../../Error/ErrorCode";

function FindID() {
  const url = `${useContext(FetchUrl)}/members/find-email`;
  const navigate = useNavigate();

  const [inputs, setInputs] = useState({
    name: "",
    nickName: "",
  });

  // Function handling submit button
  const submitHandler = (event) => {
    console.log(
      JSON.stringify({
        name: inputs.name,
        nickName: inputs.nickName,
      })
    );
    event.preventDefault();
    fetch(url, {
      method: "POST",
      body: JSON.stringify({
        name: inputs.name,
        nickName: inputs.nickName,
      }),
      headers: {
        "content-type": "application/json",
      },
    })
      .then((response) => {
        console.log(response);
        return response.json();
      })
      .then((result) => {
        console.log(result);
        if (result.message === "FIND EMAIL FAIL") {
          swal(" ");
        } else if (result.message === "FIND EMAIL SUCCESS") {
          swal(" : " + result.responseData.email);
          navigate("/login");
          //////////////////////////// .
        } else {ErrorCode(result);
        }
      })
      .catch((err) => {
        console.log("ERRROR");
      });
  };
  const handleChange = (event) => {
    const name = event.target.name;
    const value = event.target.value;
    setInputs((values) => ({ ...values, [name]: value }));
  };

  return (
    <div className="login">
      <div className="login-title">
          / <Link to="/FindPWD"> </Link> 
      </div>
      <form className="login-inputs" onSubmit={submitHandler}>
        <input
          className="input-box"
          type="text"
          name="name"
          onChange={handleChange}
          placeholder=" "
        />
        <input
          className="input-box"
          type="text"
          onChange={handleChange}
          name="nickName"
          placeholder=" "
          accept="number"
        />
        <button className="submit-btn"></button>
      </form>

      <div className="signup-btns">
        <Link to="/login">
          <div className="email">  </div>
        </Link>
      </div>
    </div>
  );
}

export default FindID;
