import React from "react";
import { useNavigate } from "react-router-dom";

import swal from "sweetalert";

function SocialLoginFail() {
  const navigate = useNavigate();
  swal(
    "   ",
    "   ",
    "warning"
  );
  setInterval(() => {
    navigate("/");
  }, [2000]);

  return <div></div>;
}

export default SocialLoginFail;
