import React, { Component } from "react";
import swal from "sweetalert";

import { FetchUrl } from "../../../../store/communication";
import ErrorCode from "../../../../Error/ErrorCode";
import { getCookie } from "../../../../Cookie";

import "./RoomReform.css";
import "./RoomReform.css";

export class RoomReform extends Component {
  constructor(props) {
    super(props);
    this.state = {
      starDistroyed: true,
      imgeRef: "",
      fileImage: "",
    };
  }

  handleChange = (event) => {
    const name = event.target.name;
    const value = event.target.value;
    this.setState((values) => ({ ...values, [name]: value }));
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const formData = new FormData();
    let outputs = {
      roomName: this.state.roomName,
      mode: this.state.mode, //MODE1, MODE2, MODE3
      pwd:
        this.state.roomPwd === null || this.state.roomPwd === ""
          ? null
          : this.state.roomPwd,
      roomDescription: this.state.description,
      roomCategory: this.state.categoryName,
      roomMemberMaxNo: this.state.num,
      endAt: this.state.endTime,
    };
    console.log(".");
    console.log(outputs);
    console.log(".");
    formData.append(
      "roomUpdateDTO",
      new Blob([JSON.stringify(outputs)], { type: "application/json" })
    );

    if (
      this.state.imgeRef !== null &&
      this.state.imgeRef !== "" &&
      this.state.imgeRef.current.files[0] !== undefined
    ) {
      formData.append("images", this.state.imgeRef.current.files[0]);
    }
    console.log("URL.");
    console.log(
      FetchUrl._currentValue +
        "/rooms/" +
        window.location.pathname.split("/")[2].substring(0) +
        "/update"
    );
    fetch(
      FetchUrl._currentValue +
        "/rooms/" +
        window.location.pathname.split("/")[2].substring(0) +
        "/update",
      {
        method: "POST",
        body: formData,
        headers: { accessToken: getCookie("accessToken") },
      }
    )
      .then((response) => {
        console.log(response);
        if (response.ok) {
          console.log("");
          return response.json(); //ok  .
        } else {
          response.json().then((errorResult) => {
            ErrorCode(errorResult);
            let errorMessage = "";
            throw new Error(errorMessage);
          });
        }
      })
      .then((result) => {
        if (result != null) {
          console.log(result);
          console.log("");
          swal(" ");
          this.exitModal();
        }
      })
      .catch((err) => {
        console.log(" POST");
        swal(" .");
      });
  };

  saveFileImage = (event) => {
    this.setState({
      fileImage: URL.createObjectURL(event.target.files[0]),
    });
  };

  //  
  getRoomDataSetting() {
    console.log("__");
    fetch(
      FetchUrl._currentValue +
        "/rooms/" +
        window.location.pathname.split("/")[2].substring(0) +
        "/settings",
      {
        headers: {
          accessToken: getCookie("accessToken"),
        },
      }
    )
      .then((response) => {
        if (response.ok) {
          return response.json(); //ok  .
        } else {
          response.json().then((errorResult) => {
            let errorMessage = "";
            throw new Error(errorMessage);
          });
        }
      })
      .then((result) => {
        if (result != null) {
          let datas = result.responseData.room;
          console.log("GETDataResult");
          console.log("GETDataResult");
          this.setState(datas);
        }
      })
      .catch((err) => {
        console.log(".");
      });

    this.setState({ starDistroyed: null });
  }

  exitModal() {
    this.setState({ starDistroyed: true });
    this.props.close();
  }

  render() {
    // , ,     
    const { open, close, header } = this.props;
    if (
      this.state.starDistroyed !== null &&
      this.state.starDistroyed === true
    ) {
      this.getRoomDataSetting();
    }
    return (
      <div className={open ? "openModal modal" : "modal"}>
        {open ? (
          <section>
            <header>
              {header}
              <button className="close" onClick={close}>
                &times;
              </button>
            </header>
            <main>
              <form onSubmit={this.handleSubmit}>
                {/*form input name, type  .   id   . input  .*/}
                <div className="form-frame">
                  <div className="room-imageReform">
                    <img
                      alt="sample"
                      src={
                        this.state.fileImage !== "" &&
                        this.state.fileImage !== null
                          ? this.state.fileImage
                          : this.state.img
                      }
                      style={{
                        position: "absolute",
                        marginTop: "55px",
                        width: "150px",
                        height: "150px",
                        borderRadius: "50%",
                        pointerEvents: "none",
                      }}
                      className="room-image__uploadReformFile"
                    />
                    {/* ,  */}
                    <input
                      type="file"
                      name="roomImage"
                      accept="image/*"
                      onChange={this.saveFileImage}
                      className="room-image__uploadReform"
                      ref={this.state.imgeRef}
                    />
                    <div className="room-image__messageReform">
                        
                    </div>
                  </div>
                  <div className="room-inforReform">
                    {/**/}

                    <div className="input-typeReform">
                      <div className="lineReform">
                        <div className="lineReformLeftDescript"></div>
                        <input
                          type="text"
                          name="roomName"
                          value={this.state.roomName || ""}
                          onChange={this.handleChange}
                          placeholder="  "
                        />
                      </div>
                      <div className="lineReform">
                        <div className="lineReformLeftDescriptLine2"></div>
                        <input
                          type="text"
                          name="description"
                          value={this.state.description || ""}
                          onChange={this.handleChange}
                          placeholder="  "
                        />
                      </div>
                      <div className="lineReform">
                        {/* <div className="lineReformLeftDescriptLine3">
                          
                        </div> */}
                        <label>
                          
                          <input
                            type="number"
                            name="num"
                            value={this.state.num ? this.state.num : ""}
                            onChange={this.handleChange}
                            accept="number"
                            min="1"
                            max="25"
                            placeholder=" (1~25)"
                          />
                        </label>
                        {/* <div className='lineReformDescriptLine4'></div> */}
                        <label>
                          
                          <select
                            name="categoryName"
                            value={this.state.categoryName || ""}
                            onChange={this.handleChange}
                          >
                            <option value="" disabled>
                               
                            </option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                          </select>
                        </label>
                      </div>
                      <div className="lineReform">
                        <span></span>
                        <input
                          type="datetime-local"
                          name="endTime"
                          max="2030-12-31T23:59"
                          value={this.state.endTime || ""}
                          onChange={this.handleChange}
                          className="endTime"
                        />

                        <span></span>
                        <label className="switch">
                          <input
                            type="checkbox"
                            name="secret"
                            value={this.state.isChecked}
                            onChange={this.handleChangeCheck}
                          />
                          <span className="slider round"></span>
                        </label>
                        {/*checkbox   .*/}
                        <input
                          type="password"
                          name="roomPwd"
                          value={this.state.roomPwd || ""}
                          onChange={this.handleChange}
                          maxLength="4"
                          minLength="0"
                          placeholder=" 4"
                        />
                      </div>
                    </div>

                    <div className="input-rules">
                      {/*.  . */}
                      <div className="rules-title"> </div>
                      <div className="rules-box-reform">
                        <label>
                          <input
                            type="radio"
                            name="mode"
                            value="MODE1"
                            checked={
                              this.state.mode === "MODE1" ? "checked" : ""
                            }
                            onChange={this.handleChange}
                          />
                          
                        </label>
                        <label>
                          <input
                            type="radio"
                            name="mode"
                            value="MODE3"
                            checked={
                              this.state.mode === "MODE3" ? "checked" : ""
                            }
                            onChange={this.handleChange}
                          />
                          
                        </label>
                        <label>
                          <input
                            type="radio"
                            name="mode"
                            value="MODE2"
                            checked={
                              this.state.mode === "MODE2" ? "checked" : ""
                            }
                            onChange={this.handleChange}
                          />
                          
                        </label>
                        <label>
                          <input
                            type="radio"
                            name="mode"
                            value="MODE4"
                            checked={
                              this.state.mode === "MODE4" ? "checked" : ""
                            }
                            onChange={this.handleChange}
                          />
                           
                        </label>
                      </div>
                    </div>
                    <button type="submit"></button>
                  </div>
                </div>
              </form>
            </main>
          </section>
        ) : null}
      </div>
    );
  }
}
